  background: #333;
  text-align: center;
  padding: 2px;
}
a{
  color: #fff;
  text-decoration: none;
}
.active  {
  color: red;
}
.myFooter i{
  font-size: 24px;

}
.myFooter h2{
  padding: 2px;
  font-size: 14px;
  font-weight: 400;
}
</style>



