import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export default new Router({
  routes: [
      {
        path: '/baoming',
        name: 'HelloWorld',
        component: HelloWorld
      },
      {
        path: '/login',
        name: 'login',
        component: () => import('@/components/kuaisulogin')

      },
    {
      path: '/mydidi',
      name: 'mydidi',
      component: () => import('@/components/mydidi')

    },
      {
        path: '/',
        name: 'shouye',
        component: () => import('@/components/shouye')
      },
      {
        path:'/chuangye',
        name:'chuangye',
        component:() => import('@/components/chuangye')
      },
      {
        path:'/pingtai',
        name:'pingtai',
        component:() => import('@/components/pingtai')
      },
    {
      path:'/news',
      name:'news',
      component:() => import('@/components/news')
    },
    {
      path:'/detailnews',
      name:'detailnews',
      component:() => import('@/components/detailnews')
    },
    {
      path:'/newproject',
      name:'newproject',
      component:() => import('@/components/newproject')
    },
    {
      path:'/gerenmsg',
      name:'gerenmsg',
      component:() => import('@/components/gerenmsg')
    },
    {
      path:'/clientmsg',
      name:'clientmsg',
      component:() => import('@/components/clientmsg')
    },
    {
      path:'/zhucekehu',
      name:'zhucekehu',
      component:() => import('@/components/zhucekehu')
    },
    {
      path:'/mymoney',
      name:'mymoney',
      component:() => import('@/components/mymoney')
    },
    {
      path:'/changepwd',
      name:'changepwd',
      component:() => import('@/components/changepwd')
    },
    ]
})



